body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
        background-color: rgb(206, 173, 236);
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}
.loader {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(255, 255, 255, 0.756);
    display: flex;
    justify-content: center;
    align-items: center;
}
.lds-dual-ring {
    display: block;
    width: 80px;
    height: 80px;
}
.lds-dual-ring:after {
    content: " ";
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid #000;
    border-color: #000 transparent #000 transparent;
    animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
* {
    box-sizing: border-box;
}
ul {
    list-style: none;
    padding: 0;
    margin: 0;
}
.row,
form {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
    align-items: center;
}
.row input {
    width: 70%;
    padding-left: 5px;
    height: 50px;
    font-size: 1.4rem;
    border-radius: 10px;
}
input.check_box {
    width: 40px;
    margin-right: 10px;
}
input.check_box:hover{
    cursor: pointer;
}
.row span {
    width: 60%;
    flex-grow: 1;
    text-align: left;
    font-size: 1.4rem;
}
.row button {
    width: 20%;
    height: 50px;
    font-size: 1.4rem;
    border-radius: 10px;
}
.row button:hover{
    cursor: pointer;
    background-color: aqua;
}
